@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:border-box}body{max-width:100vw;overflow-x:hidden;font-family:"Nunito Sans",sans-serif}.MainNav{background-color:#fafafa;box-shadow:0px 0px 5px silver;position:fixed;top:0%;width:100%;z-index:1000}.MainNav .toggleMode{cursor:pointer;text-transform:capitalize}@media (max-width: 37.99rem){.MainNav .toggleMode span{display:none}}.MainNav .nav{width:96%;margin:auto}.MainNav .nav .navbar-brand{padding-left:26px;cursor:pointer;font-weight:800;background:linear-gradient(90deg, #1ce4ff 0%, #77bdec 100%);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent}@media (max-width: 24.99rem){.MainNav .nav .navbar-brand{padding-left:0px}}.MainNav .nav-link{font-weight:800;font-size:1.5rem}@media (min-width: 37.5rem){.MainNav .nav-link{font-size:1.1rem;margin-right:20px}}.MainNav .nav-link.true{background:linear-gradient(90deg, #1ce4ff 0%, #77bdec 100%);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent}.home{max-width:90%;margin:0 auto;padding-top:6rem}.home .input-group{box-shadow:0px 0px 5px silver}.home .input-group input[type="text"]{height:8vh;outline:none !important;border:none !important;font-size:1rem;font-weight:700}.home .input-group input[type="text"]:focus{outline:none !important;box-shadow:none;outline:-webkit-focus-ring-color auto 0px}.home .input-group input[type="text"]:focus{background:#e2e6ea}.home .input-group-text{background-color:#fff}.home .dropdown button{box-shadow:0px 0px 5px silver;height:8vh;border:none !important;font-weight:700;color:#858585;text-align:left}@media (min-width: 37.5rem){.home .dropdown button{width:100%}.home .dropdown-menu{width:100%}}.home .dropdown-item{font-weight:700;transition:all 0.2s ease-in;cursor:pointer}.home .dropdown-item:focus,.home .dropdown-item:hover{color:#fff;background:#24ccfd !important}.home .card{margin-bottom:2rem;cursor:pointer}.home .card img{height:200px;width:100%;object-fit:cover}.home .card-title{color:#2b3945 !important}.home .card-text span:nth-child(1){font-size:1.1rem !important;color:#2b3945}.home .card-text span:nth-child(2){font-weight:700 !important;color:#858585}.specificPage{max-width:90%;margin:0 auto;padding-top:8rem;padding-bottom:2rem}.specificPage__title{font-size:2rem !important;font-weight:700;color:#2b3945}@media (max-width: 49.99rem){.specificPage__title{margin-top:2rem !important}}.specificPage .info span:nth-child(1){font-size:1.1rem}.specificPage .info span:nth-child(2){font-weight:700}.specificPage .info .borders{box-shadow:0px 0px 5px silver;font-size:0.9rem !important;padding:0.6rem 2.4rem !important}.specificPage .university{margin-top:5rem !important}.specificPage .university__title{font-size:2rem !important;font-weight:700;color:#2b3945}@media (max-width: 49.99rem){.specificPage .university__title{margin-top:2rem !important}}.specificPage .university th{font-weight:bold;font-size:1.1rem;text-transform:capitalize;color:#2b3945}.specificPage .university td{font-weight:bold;color:#858585}.map{margin-top:4rem}.map .leaflet-container{height:90vh;width:100%;z-index:1}.app.darkmode{min-height:100vh;background-color:#202c37}.app.darkmode .MainNav{background-color:#2b3945}.app.darkmode .MainNav .nav-link{color:#fff}.app.darkmode input[type="text"]{background-color:#2b3945;color:#fff}.app.darkmode input[type="text"]::-webkit-input-placeholder{color:#d9d9d9}.app.darkmode input[type="text"]:-ms-input-placeholder{color:#d9d9d9}.app.darkmode input[type="text"]::placeholder{color:#d9d9d9}.app.darkmode input[type="text"]:focus{background-color:#2b3945 !important}.app.darkmode .input-group-text{background-color:#2b3945;border:none;color:#fff}.app.darkmode .dropdown-toggle,.app.darkmode .dropdown-menu{background-color:#2b3945;color:#d9d9d9}.app.darkmode .dropdown-item{color:#fff !important}.app.darkmode .card{background-color:#2b3945}.app.darkmode .card-title{color:#fff !important}.app.darkmode .card-text span:nth-child(1){color:#f2f2f2}.app.darkmode .card-text span:nth-child(2){color:#d9d9d9}.app.darkmode .specificPage__title{color:#fff}.app.darkmode .specificPage .left{color:#e6e6e6}.app.darkmode .specificPage .right{color:#bfbfbf !important}.app.darkmode .specificPage .university__title{color:#fff !important}.app.darkmode .specificPage .university th{color:#e6e6e6;border-color:#2b3945 !important}.app.darkmode .specificPage .university td{border-color:#2b3945 !important;color:#bfbfbf !important}.app.darkmode .specificPage .university .message{color:#fff}

